
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'

import Collapse from '@/components/Collapse/Collapse' 
import showMore from '@/components/ShowMore/showMore'  
import Timeline from '@/components/TimeLine/timeline'  
import slide from '@/components/Slide/slide'

export default{
components: {
XHeader,
Tab,
TabItem,
Swiper,Collapse,showMore,Timeline,slide
},
data(){
return{
noData: false,
tabIndex: 0,
jgmc: "",
jgbm: "",
lsh: "",
zyh: "",
n: "",
ryInfo: {},
bcInfoDemo: [{
tip:'2012-04-12  17:34',
content:'休闲室撒大红花暗示法来看少发了哈来看少发了他 休闲室撒大红花暗示法来看少发了哈来看少发了他',
footerL:'抢救记录',
footerR:'医师签字：韩寒'
},{
tip:'2012-04-12  17:34',
content:'休闲室撒大红花暗示法来看少发了哈来看少发了他 休闲室撒大红花暗示法来看少发了哈来看少发了他',
footerL:'抢救记录',
footerR:'医师签字：韩寒'
},],
bcInfo: [],
cyInfo: {},
pageIndex: 1,
pageSize: 15,
}
},
mounted() {
this.getParams();
},
methods: {
slide(index){
this.$refs.slide.refresh();
this.$refs.slide.goToPage(index);
},

getParams() {
var routerParams = this.$route.params;
this.jgmc = routerParams.JGMC;
this.jgbm = routerParams.JGBM;
this.lsh = routerParams.LSH;
this.zyh = routerParams.ZYH;
this.n = routerParams.N;
this.getAdmissionRecord();
this.getCourseRecord(true);
this.getDischargeRecord();
},

getAdmissionRecord() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "inHospitalLog",
"MSH.2": "ry",
"CHZBM": this.$root.loginInfo.CHZBM,
"jgbm": this.jgbm,
"lsh": this.lsh,
"zyh": this.zyh,
"n": this.n,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined') {
this.ryInfo = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},

getCourseRecord(upOrDown) {
if (upOrDown) {
this.pageIndex = 1;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "inHospitalLog",
"MSH.2": "bc",
"CHZBM": this.$root.loginInfo.CHZBM,
"jgbm": this.jgbm,
"lsh": this.lsh,
"zyh": this.zyh,
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex = 1;
this.bcInfo = [];
}
if (res.rows.length > 0) {
this.noData = false;
this.pageIndex++;
for (var index = 0; index < res.rows.length; index++) {
const element = res.rows[index];
var obj = {};
obj["tip"] = element.JLRQ ? element.JLRQ : "";
obj["content"] = element.BCJLNR ? element.BCJLNR : "";
obj["footerL"] = element.JLLX ? element.JLLX : "";
obj["footerR"] = "住院医师：" + (element.ZYSMC ? element.ZYSMC : "");
this.bcInfo.push(obj);
}
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
},

getDischargeRecord() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "inHospitalLog",
"MSH.2": "cy",
"CHZBM": this.$root.loginInfo.CHZBM,
"jgbm": this.jgbm,
"lsh": this.lsh,
"zyh": this.zyh,
"n": this.n,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined') {
this.cyInfo = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
}
}



